:root {
    --gray: #666;
    --dark: #333;
    --black: #000;
    --white: #fff;
    --muted: #6c757d;
    --light: #f7f7f7;
    --accent: #047786;
    --primary: #df2a63;
    --accent-dark: #011114;
    --accent-light: #E4F1F1;
    --accent-secondary: #069eb2;
    --body: var(--dark)
}

.navbar-toggler {
    background-color: #f7f7f7 !important;
    background-color: var(--light) !important;
    color: #000 !important;
    color: var(--black) !important;
}

.segment-button-checked {
    background-color: #fff !important;
    background-color: var(--white) !important;
    background: #fff !important;
    background: var(--white) !important;
    color: #df2a63 !important;
    color: var(--primary) !important;
}

div, li, p, div, h1, h2, h3, h4, h5, h6, .btn {
    color: #333;
    color: var(--body);
}

#field-wrapper {
    border: 1px solid rgba(248, 249, 250, 0.5);
}

.input-group  {
    border-radius: 25px !important;
}

.input-group-text {
    border-radius: 25px 0px 0px 25px;
}

.input-group, .input-group .input-group-text {
    background-color: #f7f7f7 !important;
    background-color: var(--light) !important;
}

.form-control {
    color: #000;
    color: var(--black);
    border-color: #047786;
    border-color: var(--accent);
    background-color: #f7f7f7 !important;
    background-color: var(--light) !important;
}

.form-control:focus {
    color: #000;
    color: var(--black);
    border-color: #047786;
    border-color: var(--accent);
    background-color: #fff !important;
    background-color: var(--white) !important;
}

.card {
    background-color: #fff;
    background-color: var(--white);
}

.modal-content {
    background-color: #fff;
    background-color: var(--white);
}

/** Buttons **/

.btn.border {
    border-color: #000 !important;
    border-color: var(--black) !important;
}

.btn-primary, .btn-outline-primary:hover {
    color: white !important; 
    border: 1px solid #df2a63 !important; 
    border: 1px solid var(--primary) !important;
    background-color: #df2a63 !important;
    background-color: var(--primary) !important;
}

.btn-outline-primary {
    border: 1px solid #df2a63 !important;
    border: 1px solid var(--primary) !important;
    color: #df2a63 !important;
    color: var(--primary) !important;
}

.btn-outline-accent {
    border: 1px solid #047786 !important;
    border: 1px solid var(--accent) !important;
    color: #047786 !important;
    color: var(--accent) !important;
}

.btn.border {
    border: 1px solid #000 !important;
}

.btn-accent { 
    background-color: #047786 !important; 
    background-color: var(--accent) !important;
    border: 1px solid #047786 !important;
    border: 1px solid var(--accent) !important;
    color: white !important;
}

/** Border **/
.border-primary {
    border-color: #df2a63 !important;
    border-color: var(--primary) !important;
}

.border-secondary {
    border-color: #047786 !important;
    border-color: var(--accent) !important;
}

.border-dark {
    border-color: #333 !important;
    border-color: var(--dark) !important;
}

/** Background **/

.bg-dark {
    background-color: #011114 !important;
    background-color: var(--accent-dark) !important;
}

.bg-success {
    background-color: #047786 !important;
    background-color: var(--accent) !important;
}

.bg-white {
    background-color: #fff !important;
    background-color: var(--white) !important;
}

.bg-black {
    background-color: #000;
}

.bg-gray {
    background-color: #666;
    background-color: var(--gray);
}

.bg-primary {
    background-color: #df2a63 !important;
    background-color: var(--primary) !important;
}

.bg-gradient {
    background-image: linear-gradient(90deg, #069eb2, #DED240);
}

.bg-accent {
    background-color: #047786;
    background-color: var(--accent);
}

.bg-accent-light {
    background-color: #E4F1F1;
    background-color: var(--accent-light);
}

.bg-light {
    background-color: #f7f7f7 !important;
    background-color: var(--light) !important;
}

/** Text **/

.text-accent-secondary {
    color: #069eb2 !important;
    color: var(--accent-secondary) !important
}

.text-gradient {
    background-image: linear-gradient(90deg, #df2a63, #069eb2);
    background-image: linear-gradient(90deg, var(--primary), var(--accent-secondary));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
}

.text-muted {
    color: #6c757d !important;
    color: var(--muted) !important;
}

.text-dark {
    color: #333 !important;
    color: var(--dark) !important;
}

.text-accent {
    color: #047786;
    color: var(--accent);
}

.text-primary {
    color: #df2a63 !important;
    color: var(--primary) !important;
}

/** General **/

a, .text-link {
    color: #047786 !important;
    color: var(--accent) !important;
}

.menu-category:hover {
    background-color: #047786;
    background-color: var(--accent);
}

.menu-category:active {
    box-shadow: none;
    background-color: #047786;
    background-color: var(--accent);
}

.menu-category:focus {
    box-shadow: none;
    background-color: #047786;
    background-color: var(--accent);
}

.nav-item:hover {
    background-color: #047786;
    background-color: var(--accent);
    color: #000;
}

.dropdown-item.active,
.dropdown-item:active {
    background-color: #047786;
    background-color: var(--accent);
    color: #fff;
}

.user {
    background-color: #047786;
    background-color: var(--accent);
}

.nav-item .mobile:focus {
    background-color: #047786;
    background-color: var(--accent);
}

.image-input-container {
    border: 2px solid #047786;
    border: 2px solid var(--accent);
}

.user-menu {
    background-color: #666;
    background-color: var(--gray);
}


.hover-light:hover {
    color: #333 !important;
    color: var(--dark) !important;
    background-color: #f7f7f7;
    background-color: var(--light);
}

.status-badge {
    background-color: #E4F1F1;
    background-color: var(--accent-light);
}

/* Ionic */

.ion-color-accent {
    background-color: #E4F1F1 !important;
    background-color: var(--accent-light) !important;
    padding: 4px 6px;
}

/** Hover **/

.hover-success:hover {
    color: white !important;
    background-image: linear-gradient(90deg, #df2a63, #069eb2);
    background-image: linear-gradient(90deg, var(--primary), var(--accent-secondary));
}
.hover-success:hover * {
    color: white !important;
}
